.projects {
  margin-top: 30px;
  width: 80%;
}

.projects h1 {
  margin-bottom: 20px;
}
.project-list {
  display: grid;
  grid-template-columns: 290px 290px 290px;
  grid-template-rows: 50% 50%;
  width: 90%;
}

.project-item {
  padding: 20px;
  border-radius: 0.8px;
  height: 230px;
  width: 250px;
  box-shadow: inset 0px 0px 2px 0px rgba(0,0,0,0.3);
}

.gray-effect {
  padding: 20px;
  background-color: rgba(41, 40, 40, 0.3);
  z-index: 2;
  position: absolute;
  height: 230px;
  width: 250px;
  -webkit-transition: all 300ms ease-in-out;
  -o-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}

.gray-effect:hover {
  background-color: rgba(132, 132, 142, 0);
}

.links {
  position: absolute;
  top: 20px;
  right: 20px;
  display: flex;
}

.links a {
  text-decoration: none;
  color: black;
}

.links a:hover {
  filter: opacity(72%);
}

.links a:first-child {
  margin-top: 1.2px;
}

.gray-effect img {
  height: 20px;
  margin-left: 10px;
}

.gray-effect img:hover {
  filter: opacity(72%);
}

.thumbnail {
  display: flex;
  align-items: center;
  justify-content: center;
}

.thumbnail img{
  height: 100%;
  width: 100%;
  object-fit: cover;
}

h3 {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}


p#project {
  margin-top: 10px;
  color: black;
  font-size: 20px;
  font-family: Inconsolata, monospace;
}

@media only screen and (max-width: 700px) {
  .projects {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .project-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .thumbnail {
    display: none;
  }

}
