@import url(https://fonts.googleapis.com/css?family=Inconsolata:400,400i|Caveat);
/* Css reset */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* styling */
body {
  font-family: 'Inconsolata', monospace;
  font-weight: 400;
	background: #050505;
	margin-top: 60px;
	margin-left: 10px;
	margin-right: 10px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

h1 {
	font-size: 36px;
	color: #CED1DA;
}

h2 {
	font-size: 22px;
	color: #3FDE9A;
}

p {
	font-family: 'Caveat', monospace;
	font-size: 25px;
	font-weight: 400;
	color: #CED1DA;
	line-height: 1.3;
}

span#red {
	color: #FA2D57;
}

div.info {
	display: grid;
	grid-template-columns: 41% 18% 41%;
	text-align: right;
	width: 100%;
}

@media only screen and (max-width: 800px) {

	div.info {
		display: flex;
		flex-direction: column;
		text-align: center;
		align-items: center;
	}

	div.info > * {
		margin: 10px;
	}

}

div.about {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.profile {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.img-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.img-wrapper img {
  width: 110px;
  height: 120px;
  object-fit: cover;
  border-radius: 50%;
  border: 1px solid #CED1DA;
}

 img#shades {
  rotate: -10deg;
  border: 0px;
  width: 64px;
  height: 64px;
  object-fit: none;
  z-index: 2;
  position: absolute;
  padding-bottom: 12px;
}

img.show {
  display: block;
}

img.hide {
  display: none;
}

.social {
  display: flex;
  margin: 10px;
}

.social img {
  width: 25px;
  margin: 5px 10px 0px 10px;
}

.technologies {
  display: flex;
  flex-wrap: wrap;
  width: 180px;
  justify-content: flex-end;
}
.tech-item {
  color: black;
  height: 20px;
  padding: 8px 10px 8px 10px;
  text-align: center;
  align-content: center;
  border-radius: 0.8px;
  line-height: normal;
}

.tech-item#id2 {
  margin-right: -20px;
  margin-left: 20px;
}

.tech-item#id8 {
  margin-right: 20px;
}

.tech-item#id10 {
  margin-right: -20px;
}

.projects {
  margin-top: 30px;
  width: 80%;
}

.projects h1 {
  margin-bottom: 20px;
}
.project-list {
  display: grid;
  grid-template-columns: 290px 290px 290px;
  grid-template-rows: 50% 50%;
  width: 90%;
}

.project-item {
  padding: 20px;
  border-radius: 0.8px;
  height: 230px;
  width: 250px;
  box-shadow: inset 0px 0px 2px 0px rgba(0,0,0,0.3);
}

.gray-effect {
  padding: 20px;
  background-color: rgba(41, 40, 40, 0.3);
  z-index: 2;
  position: absolute;
  height: 230px;
  width: 250px;
  transition: all 300ms ease-in-out;
}

.gray-effect:hover {
  background-color: rgba(132, 132, 142, 0);
}

.links {
  position: absolute;
  top: 20px;
  right: 20px;
  display: flex;
}

.links a {
  text-decoration: none;
  color: black;
}

.links a:hover {
  filter: opacity(72%);
}

.links a:first-child {
  margin-top: 1.2px;
}

.gray-effect img {
  height: 20px;
  margin-left: 10px;
}

.gray-effect img:hover {
  filter: opacity(72%);
}

.thumbnail {
  display: flex;
  align-items: center;
  justify-content: center;
}

.thumbnail img{
  height: 100%;
  width: 100%;
  object-fit: cover;
}

h3 {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}


p#project {
  margin-top: 10px;
  color: black;
  font-size: 20px;
  font-family: Inconsolata, monospace;
}

@media only screen and (max-width: 700px) {
  .projects {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .project-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .thumbnail {
    display: none;
  }

}

