.profile {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.img-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.img-wrapper img {
  width: 110px;
  height: 120px;
  object-fit: cover;
  border-radius: 50%;
  border: 1px solid #CED1DA;
}

 img#shades {
  rotate: -10deg;
  border: 0px;
  width: 64px;
  height: 64px;
  object-fit: none;
  z-index: 2;
  position: absolute;
  padding-bottom: 12px;
}

img.show {
  display: block;
}

img.hide {
  display: none;
}

.social {
  display: flex;
  margin: 10px;
}

.social img {
  width: 25px;
  margin: 5px 10px 0px 10px;
}
